.Sheets {   
    width: 100%;
    height: 85%;
}

.Sheets .historyList {
    width: 100%;
    border-top: 1px dashed #888;
    height:86%;
    overflow: scroll;
}

.Sheets .historyList .saved-sheets {
    display: flex;
    flex-wrap: wrap;
    height: 45px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    width: 98%;
    border: 1px solid #888;
    border-radius: 5px;
}

.Sheets .historyList .saved-sheets p {
    padding: 6px;
    flex: 1 50px;
    font-size: 25px;
    margin-top: 5px;
    cursor: pointer;
}

.Sheets img {
    padding: 5px;
    height: 30px;
    width: 30px;
    cursor: pointer;
}

.Sheets label {
    font-size: 30px;
    padding: 10px;
    cursor: pointer;
}

.addSheet {
    width: 100%;
    height: 40px;
    font-size: 25px;
    margin-top: -50px;
    border: none;
    border-top: 1px dashed #888;
    cursor: pointer;
}