.show-armor {
    background: none;
    height: 90%; width: 100%;
    margin-left: 15px;
    display: flex;
}

.show-armor img {
    background: none;
    height: 125px; width: 125px;
}

.show-armor .form-armor {
    background: none;
    flex-direction: column;
    width: 150px;
}

.show-armor .form-armor p {
    background: none;
    text-align: start;
    width: 120px;
    font-size: 20px;
}

.show-armor .form-armor input {
    background: none;
    width: 150px;
    height: 25px;
    border: 1px solid #fff;
    font-size: 20px;
    padding: 5px;
}